/*---rating ---*/
.rating {
  display: -webkit-inline-box;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  flex-direction: row-reverse;
  list-style: none;
  margin: 0;
  padding: 0;
}
.rating input {
  display: none;
}
.rating label {
  color: #f3f6f9;
  cursor: pointer;
}
.rating label::before {
  margin-right: 5px;
  font-size: 20px;
  display: inline-block;
}

.rating-static {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
}
.rating-static label {
  cursor: default;
}
.rating-static label:not(.active) {
  color: #f3f6f9 !important;
}

.rating-xs label::before {
  margin-right: 1px;
  font-size: 1rem;
}

.rating-sm label::before {
  margin-right: 2px;
  font-size: 1.143rem;
}

.rating-lg label::before {
  font-size: 1.714rem;
}

.rating-remove {
  margin-left: 10px;
  margin-right: 10px;
  cursor: pointer;
  line-height: 20px;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
}

[data-has-rate="true"]:hover .rating-remove {
  opacity: 0.6;
  visibility: visible;
}
[data-has-rate="true"]:hover .rating-remove:hover {
  opacity: 1;
}

.rating-secondary .active,
.rating-secondary :checked ~ label,
.rating-secondary label:hover,
.rating-secondary label:hover ~ label {
  color: #4d7bc9;
}

.rating-gray .active,
.rating-gray :checked ~ label,
.rating-gray label:hover,
.rating-gray label:hover ~ label {
  color: gray;
}

.rating-dark .active,
.rating-dark :checked ~ label,
.rating-dark label:hover,
.rating-dark label:hover ~ label {
  color: #172b4c;
}

